		font-weight: 700;
	}

	.news-text,
	.section-news .news-text {
		font-size: 16px;
	}
}

@media (max-width: 1023px) {
	.nav-wrapper .select-wrapper input.select-dropdown,
	nav .nav-wrapper ul a {
		font-size: 1rem;
	}

	.navbar-fixed nav .brand-logo {
		right: 0;
		left: auto;
	}

	.side-nav {
		width: 200px;
	}

	.side-nav a .header-select {
		margin-left: -15px;
		margin-right: -15px;
	}

	.header-select {
		width: 150px;
	}
}



